* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App h1 {
  text-align: center;
  margin: 10px;
}
.template {
  width: 33.3333%;
  display: inline-block;
  border: 10px solid transparent;
}
.template .image {
  width: 100%;
  height: 350px;
  border: 1px solid black;
  background-size: cover;
  background-position: center;
}
.meme img {
  width: auto;
  height: 350px;
  border: 1px solid black;
}
.meme {
  text-align: center;
}
.meme input {
  margin: 10px;
  padding: 5px;
  border-radius: 2px;
  border-color: brown;
}
.meme button {
  padding: 5px;
  margin: 10px;
}
